<template>
  <div>
    <h1>파티 찾기 도우미에 오신 것을 환영합니다!</h1>
    <section>
        <h2>&lt;파티 찾기 도우미 테스트 안내&gt;</h2>
        <div>현재 비밀번호 정보를 평문으로 저장하는 것은 정식으로 출시하기 이전에 암호화하도록 할 예정입니다.</div>
        <div>(현재는 입력을 할 수도 없지만) 베타 테스트 도중에 입력된 정보는 정식 출시와 동시에 초기화될 예정입니다.</div><br>
        <div>어떤 건의 사항이든지 우측 상단의 Developer Contact로 보내주셔도 좋습니다.</div>
        <div class="bold">특히나 아래 내용에 건의 사항이 있다면, 보내주시길 부탁드립니다.</div>
        <ol class="concern">
          <li>사사게 제목 언급 횟수를 보여주지 않고 사사게 검색 결과를 보여주는 링크만 추가할지 고민하고 있습니다.</li>
          <li>파티 등록 페이지는 어떻게 구성할지 전혀 감이 잡히지 않고 있습니다. 다양한 의견 부탁드립니다.<br>
          <span class="bold">파티 등록이란?</span> 5명 정보를 올리고, 3명을 구하던지하는 식으로 이미 파티가 어느정도 짜여있는 상태<br>
          또는 1명이 공대장 비슷한 역할로서 나머지 파티원들을 구하려고 하는 경우 등을 등록하는 것</li>
        </ol>
        <h2>&lt;파티 찾기 도우미 공지&gt;</h2>
        <ul class="notice">
            <li id="sasagae">사사게 기록은 닉네임 언급 여부일 뿐, <span class="bold">서버가 다른 캐릭터일 수 있습니다.</span><br>
            심지어는 <span class="bold">단순히 문장에 해당 글자가 들어간 것</span> 일 수도 있습니다.<br>
            또한, 어떠한 행동을 했는지는 확인하지 않은 내역입니다.</li>
            <li>게시글 내용에 대한 사실 여부를 보증하지 않습니다. 직접 연락 후 확인하세요.</li>
        </ul>
    </section>
  </div>
</template>

<script>
export default {
  name: 'mainPage',
  data () {
    return {

    }
  }
}
</script>

<style scoped>
h1, h2 {
  font-weight: normal;
}
ol.concern,
ul.notice {
  display: inline-block;
}
ul {
  padding: 0;
}
li {
  margin: 5px 10px;
}
li#sasagae {
  color: rgb(255, 0, 0);
}
.bold{
  font-weight: bold;
}
</style>



