export default ({
  jobList: {
    tank: [
        { korName: '나이트', engName: 'pld' },
        { korName: '전사', engName: 'war' },
        { korName: '암흑기사', engName: 'drk' },
        { korName: '건브레이커', engName: 'gnb' },
    ],
    healer: [
        { korName: '백마도사', engName: 'whm' },
        { korName: '학자', engName: 'sch' },
        { korName: '점성술사', engName: 'ast' },
    ],
    melee: [
        { korName: '몽크', engName: 'mnk' },
        { korName: '용기사', engName: 'drg' },
        { korName: '닌자', engName: 'nin' },
        { korName: '사무라이', engName: 'sam' },
    ],
    ranged: [
        { korName: '음유시인', engName: 'brd' },
        { korName: '기공사', engName: 'mch' },
        { korName: '무도가', engName: 'dnc' },
    ],
    caster: [
        { korName: '흑마도사', engName: 'blm' },
        { korName: '소환사', engName: 'smn' },
        { korName: '적마도사', engName: 'rdm' },
    ]
  },
  nicknameRegex: /^[가-힣A-Z']{1}[가-힣a-z']{1,8}$/,
  openkakaoRegex: /^(https?:\/\/)?(www\.)?open.kakao.com\/o\/[a-zA-Z0-9]{8}/,
  discordRegex: /^(.{2,32}#[0-9]{4}$)|((https?:\/\/)?(www\.)?(discord\.(gg|io|me|li)|discordapp\.com\/invite)\/.+)/,
  sasageURL: function(nickname) {
    return encodeURI('http://www.inven.co.kr/board/ff14/4485?name=subject&keyword=' + nickname);
  },
  FFLogURL: function(world, nickname) {
    world = world.substr(2); // remove 'kr' in front of real world name
    return encodeURI(`https://ko.fflogs.com/character/kr/${world}/${nickname}`);
  },
})


